<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h5 class="modal-title" v-if="AddEditText === 'Edit'">{{ $t('EditBrand') }}</h5>
                        <h5 class="modal-title" v-else>{{ $t('AddBrand') }}</h5>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" ref="AddBrand" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                        <div class="col-lg-6">                          
                            <div class="py-2"><small><span class="text-danger">
                                {{$t('MandatoryString')}}</span></small>
                            </div>                            
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import LeadDataService from "../../../../services/DataService";
export default {
    props: {
        modalAddEditItem: null
    },
    data() {
        return {
            AddEditText: '',
            txtBrandId: 0,
            modal: null,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('brand_name'),
                            name: "brand_name",
                            value: "",
                            placeholder: this.$t('Enterbrand_name'),
                            validationRules: { "required": true, max: 400 },
                        },
                        {
                            astype: "TextAreaField",
                            label: this.$t('Description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: "",
                        },
                    ],
                }

            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Submit",                  
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],

        }
    },
    created: function () {
        this.modal = this.modalAddEditItem;
        if (this.modal != null) {
            this.FetchAddEditBrandData(this.modal.brand_id);
        }
    },
    methods: {
        Close: function () { 
            this.$parent.ClosePopUp();
        },
        FetchAddEditBrandData: function (id) {
            var vm = this;
            this.AddEditText = "Edit";
            LeadDataService.FetchAddEditBrandData(id).then(response => {
                if (response.data.data != null) {
                    vm.txtBrandId = response.data.data[0].brand_id;
                    vm.FormSchema[0].Data[0].value = response.data.data[0].brand_name;
                    vm.FormSchema[0].Data[1].value = response.data.data[0].brand_description;
                }
            });
        },
        onSubmit(formfields, val) {
            var vm = this;
            vm.isLoading=true;
            var fullJson = {
                brand_id: this.txtBrandId,
                brandname: formfields.brand_name,
                branddescription: formfields.Description,
                STATUS_ID: 1001,
            };
            var postJSON = {
                data: fullJson,
            };
            var objectPostString = JSON.stringify(postJSON);
            LeadDataService.SaveBrand(objectPostString).then(function (response) {
                if (response.data[0].Status == "Success") {
                    if (vm.AddEditText == "Edit") {
                        vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Brand')]), "Success", true, vm.$t("Alert"));
                    }
                    else {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Brand')]), "Success", true, vm.$t("Alert"));
                    }
                    vm.$parent.FetchData();
                    vm.Close();
                    vm.isLoading=false;
                }
                else {
                    vm.ShowAlert(vm.$t('Error', [vm.$t('Brand')]), "Warning", true, vm.$t("Alert"));
                    vm.Close();
                    vm.isLoading=false;
                }
            })
        }

    }
}
</script>
